<!---->

<template> 
    <!---->
    <el-tree v-loading="loading" ref="tree" 
            :data="data" 
            :show-checkbox="checkbox" 
            :expand-on-click-node="false" 
            @node-click="handleNodeClick" 
            :highlight-current="selectable" 
            :props="defaultProps"
            @node-expand="handleNodeExpand">
    </el-tree>
</template>

<script>
    import treeUtils from '../../../utils/treeUtils'

    export default{
        props: {
            onNodeClick: Function,
            value: Array,
            req:Function,
            mark:Object,   // {typemenu,name:'name'}
            checkbox:false,
            nodeExpand:Number
        },
        data () {
            return {
                data: this.value,
                loading: false,
                selectable: true, // 
                type:this.type,
                 defaultProps: {
                    children: 'children',
                    label: 'label'
                }
            }
        },
        created () {
            console.log('this.nodeExpand',this.nodeExpand);
            
             //
                function func(arr) {
                    arr.forEach(v=>{ //
                        if(v.pid != 0){
                            let t = find(arr,'id',v.pid);
                            t&&(t.children?t.children.push(v):t.children=[v])
                        }
                    });
                    return arr.filter(v=>{  //
                        return v.pid == 0;
                    })
                }
                function find(arr,key,value){  //children
                    for(let i =0;i<arr.length;i++){
                        if(arr[i][key]==value){
                        return arr[i]
                        }
                    }
                    return false;
                }
               
            //  XX_name->label,id>value
            function transfor(arr,name){ 
                arr.forEach(v=>{
                    v['label']=v[name]
                    v['value']=v['id']
                })
               return arr
            }
            // this.req({id : 'tree', type :'menu', filter : true , pid :-1 , level:-1, pagesize:-1}).then(ret=>{ 
            //     // console.log(ret.data);
            //     let data=transfor(ret.data,this.mark.name)
            //     this.data=func(data)
            //     // xmview.setContentLoading(false)
            // })
            this.getData(0)
        },
        methods: {
            // 
            getData(pid){
                // console.log(ret.data);
                    if(this.nodeExpand){
                        this.req(pid).then(ret=>{ 
                            console.log('');
                            ret.data.forEach(v => {
                                console.log(this.data);
                                console.log(v);
                                this.data.push({
                                    data: v,
                                    label: v.name,
                                    value: v.id,
                                    children: v.level>2 ? null :[{
                                        label: '',
                                        value: -1,
                                    }]  //
                                    // children: v.ended ? null : [{
                                    //     label: '',
                                    //     value: -1,
                                    // }]  //
                                })
                            })
                        })

                    }
                    else{
                        this.req({id : 'tree', type :'menu', filter : true , pid :-1 , level:-1, pagesize:-1}).then(ret=>{
                            let data=transfor(ret.data,this.mark.name)
                            //
                            this.data=func(data)
                        })
                    }
                  
                    // xmview.setContentLoading(false)
            },

            handleNodeExpand (data, node, nodeDom) { //  
                // children [...] 

                //
                console.log(data, node, nodeDom);
    
                this.getData(node.data.value).then(ret=>{
                    // debugger
                    let arr = []
                    
                    ret.data.forEach(v => {
                        arr.push({
                            data: v,
                            label: v.name,
                            value: v.id,
                            children: v.level>2? null : [{ 
                                label: '',
                                value: -1,
                            }]  //
                        })
                })
                
                node.data.children = arr
                this.loading = false
                xmview.setContentLoading(false)
                })
            },
            handleNodeClick (data, node, store) { //
                 this.$emit('onNodeClick', {data, node, store})// 
                 if(this.onNodeClick) this.onNodeClick(1, data, node, store) 

                //   
                if (data.value == 0) return
                this.selectable = true
            },
            // 
            clearSelected () {
                this.selectable = false
            },

            removeItem (item, parent) {
                // children 
                if (!parent.data.children) {
                    this.data = this.data.filter((curr) => {
                        return curr.value != item.value
                    })
                } else {
                    parent.data.children = parent.data.children.filter((curr) => {
                        return curr.value != item.value
                    })

                    if (parent.data.children.length < 1) parent.data.children = null
                }
                //   
                this.$emit('input', this.data)
            },
            setCurrVal (val) { //
                if (val === this.data) return
                this.data = val
                this.$emit('input', val)
            },
 
        },
    }
      function findItem(arr,key,value){
        arr.forEach(item=>{
            if(item[key] == value ){
                return item
            }
        })
    }
</script>


